import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import {   resetPasswordAsync, selectError, selectPasswordReset } from "../authSlice";
import { Link } from "react-router-dom";

export default function ResetPassword() {
  const query = new URLSearchParams(window.location.search)
  const token = query.get('token');
  const email = query.get('email')

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
  const dispatch = useDispatch()
  const passwordReset = useSelector(selectPasswordReset);
  const error = useSelector(selectError)
  console.log(token,email);
  return (
    <>
      {(email&&token)?<div>
        <div>
          <div className="flex min-h-full flex-1 flex-col justify-center px-6 py-12 lg:px-8">
            <div className="sm:mx-auto sm:w-full sm:max-w-sm">
              <img
                className="mx-auto h-10 w-auto"
                src="/app-logo.png"
                alt="Your Company"
              />
              <h2 className="mt-10 text-center text-2xl font-bold leading-9 tracking-tight text-gray-900">
                Enter New Password.
              </h2>
            </div>

            <div className="mt-10 sm:mx-auto sm:w-full sm:max-w-sm">
              <form
                noValidate
                className="space-y-6"
                onSubmit={handleSubmit((data) => {
                  //Implementation Will be in Backend
                  console.log(data);
                  dispatch(resetPasswordAsync({email, token, password: data.password}))
                })}
              >
               {/* Password Section Starts Here */}
              <div>
                <div className="flex items-center justify-between">
                  <label
                    htmlFor="password"
                    className="block text-sm font-medium leading-6 text-gray-900"
                  >
                   New Password
                  </label>
                </div>
                
                <div className="mt-2">
                  <input
                    id="password"
                    {...register("password", {
                      required: "Password Is Required..!!",
                      pattern: {
                        value:
                          /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{8,}$/gm,
                        message: `Password Must Contain: 
                         - at least 8 characters \n
                         - must contain at least 1 uppercase letter, 1 lowercase letter, and 1 number \n
                         - Can contain special characters \n
                        `,
                      },
                    })}
                    type="password"
                    className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-sky-600 sm:text-sm sm:leading-6"
                  />
                  {errors.password && (
                    <p className="text-red-500">{errors.password.message}</p>
                  )}
                </div>
              </div>
              {/* Password Section Ends Here */}

              {/*Confirm Password Section Starts Here */}
              <div>
                <div className="flex items-center justify-between">
                  <label
                    htmlFor="password"
                    className="block text-sm font-medium leading-6 text-gray-900"
                  >
                    Confirm New Password
                  </label>
                </div>
                <div className="mt-2">
                  <input
                    id="confirmPassword"
                    {...register("confirmPassword", {
                      required: "Confirm Password Is Required..!!",
                      validate: (value, formValues) => value === formValues.password || "Password does not match."
                    })}
                    type="password"
                    className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-sky-600 sm:text-sm sm:leading-6"
                  />
                  {errors.confirmPassword && (
                    <p className="text-red-500">
                      {errors.confirmPassword.message}
                    </p>
                  )}
                  {passwordReset && (
                    <p className="text-green-500">
                      Password Reset Successfully...!!
                    </p>
                  )}
                  {error && (
                    <p className="text-red-500">
                      {error}
                    </p>
                  )}
                </div>
              </div>
              {/*Confirm Password Section Ends Here */}


               

                <div>
                  <button
                    type="submit"
                    className="flex w-full justify-center rounded-md bg-sky-600 px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-sky-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-sky-600"
                  >
                    Reset Password
                  </button>
                </div>
              </form>


              <p className="mt-10 text-center text-sm text-gray-500">
              Already a member?{" "}
              <Link
                to="/login"
                className="font-semibold leading-6 text-sky-600 hover:text-sky-500"
              >
                Log In Your Account.
              </Link>
            </p>
             
            </div>
          </div>
        </div>
      </div>:<p>Incorrect Link</p>}
    </>
  );
}
