export function fetchProductById(id) {
  return new Promise(async (resolve) => {
    const response = await fetch("/products/" + id);
    const data = await response.json();
    resolve({ data });
  });
}

export function createProduct(product) {
  return new Promise(async (resolve) => {
    const response = await fetch("/products/", {
      method: "POST",
      body: JSON.stringify(product),
      headers: { "content-type": "application/json" },
    });
    const data = await response.json();
    resolve({ data });
  });
}
export function updateProduct(update) {
  return new Promise(async (resolve) => {
    const response = await fetch(
      "/products/" + update.id,
      {
        method: "PATCH",
        body: JSON.stringify(update),
        headers: { "content-type": "application/json" },
      }
    );
    const data = await response.json();
   
    resolve({ data });
  });
}

export function fetchProductsByFilters(filter, sort, pagination, admin) {
  //filter = {"category": ["smartphones",laptops]}
  //sort = {_sort: "price", _order= "desc"}
  //pagination=  {_page: "1", _limit= 10}
  let queryString = "";

  // console.log(sort)

  for (let key in filter) {
    const categoryValues = filter[key];
    if (categoryValues.length) {
      queryString += `${key}=${categoryValues}&`;
    }
  }
  for (let key in sort) {
    queryString += `${key}=${sort[key]}&`;
    // console.log(queryString)
  }
  for (let key in pagination) {
    queryString += `${key}=${pagination[key]}&`;
    // console.log(queryString)
  }
  if(admin){
    queryString += `admin=true`;

  }

  // console.log(`fetching: /products?${queryString}`);

  return new Promise(async (resolve) => {
    const response = await fetch(
      "/products?" + queryString
    );
    const data = await response.json();
    const totalItems = await response.headers.get("X-Total-Count");
    resolve({ data: { products: data, totalItems } });
  });
}

//Pagination URL: /products?_page=1&_limit=10
// Sort Url     : /products?_sort=price&_order=asc/desc
// Filter Url   : /products?category=furniture&

